$card-color: $text !default;
$card-background-color: $scheme-main !default;


$card-header-background-color: transparent !default;
$card-header-color: $text-strong !default;
$card-header-padding: 0.75rem 1rem !default;
$card-header-shadow: 0 0.125em 0.25em rgba($scheme-invert, 0.1) !default;
$card-header-weight: $weight-bold !default;

$card-content-background-color: transparent !default;
$card-content-padding: 1.5rem !default;

$card-footer-background-color: transparent !default;
$card-footer-border-top: 1px solid $border-light !default;
$card-footer-padding: 0.75rem !default;

$card-media-margin: $block-spacing !default;

.card {
  background-color: $card-background-color;
  box-shadow: $shadow-general;
  color: $card-color;
  max-width: 100%;
  border-radius: $radius-large;
  position: relative;
}

.card-header {
  background-color: $card-header-background-color;
  align-items: stretch;
  display: flex;
}

.card-header-title {
  align-items: center;
  color: $card-header-color;
  display: flex;
  flex-grow: 1;
  font-weight: $card-header-weight;
  padding: $card-header-padding;

  &.is-centered {
    justify-content: center;
  }
}

.card-header-icon {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  padding: $card-header-padding;
}

.has-hover-blue:hover>.card-image>.image>.has-background-grey-light{
  background-color: hsl(212, 100%, 36%) !important;
}
.has-hover-yellow:hover>.card-image>.image>.has-background-grey-light{
  background-color: hsl(49, 100%, 50%) !important;
}
.has-hover-red:hover>.card-image>.image>.has-background-grey-light{
  background-color: hsl(357, 69%, 50%) !important;
}
.card-image {
  border-radius: 20px;
  display: block;
  position: relative;
}

.card-content {
  background-color: $card-content-background-color;
  padding: $card-content-padding;

  &>.content>.date{
    padding: 0.2rem 0.6rem;
    border-radius: 1rem;
    background-color: $grey-lighter;
  }
}

.card-footer {
  background-color: $card-footer-background-color;
  border-top: $card-footer-border-top;
  align-items: stretch;
  display: flex;
}

.card-footer-item {
  align-items: center;
  display: flex;
  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 0;
  justify-content: center;
  padding: $card-footer-padding;

  &:not(:last-child) {
    @include ltr-property("border", $card-footer-border-top);
  }
}

// Combinations

.card {
  .media:not(:last-child) {
    margin-bottom: $card-media-margin;
  }
}
