.leaflet-container {
    width: 100%;
    height: 100%;

  @for $i from 1 through 100 {
    &.vh#{$i}{
      height: #{$i}vh;
    }
  }
}

.leaflet-control-attribution{
  display: none;
}

.leaflet-top{
  z-index: 400;
}



.box>.leaflet-container, .vh60>.leaflet-container{
  border-radius: 8px;
}